import React from 'react';
import Pagination from './Pagination.js';
import { Button, ButtonGroup } from 'reactstrap';
import { Table, Input, NavItem, NavLink } from 'reactstrap';

export default class Course extends React.Component {

  constructor(props) {

    super(props);
    this.onClickUpdate = this.onClickUpdate.bind(this);
    this.slice = this.slice.bind(this);
    this.sendCourse = this.sendCourse.bind(this)
    this.state = {course: props.searchResult, courseSlice: [], pageCount: 0, courseSelected: []}

  }

  showCourseTime(course) {

    var day_mapping = {
      '001':'週一',
      '010':'週二',
      '011':'週三',
      '100':'週四',
      '101':'週五',
    }

    var start_index = 3;
    var end_index = 0;
    var result = ""
    for(var k=0; k<course.session.length; k++) {
      if(k>=1){
        result += '\n'
      };
      for(var i=3; i<course.session[k].length; i++) {
        if(course.session[k][i] ===  '1') {
          start_index = i;
          break;
        }
      }

      for(var i=start_index; i<course.session[k].length; i++) {
        end_index = i;
        if(course.session[k][i] === '0') {break;}
      }
      // string.substring(start, end)
      var start_time = 6 + start_index - 3;
      var end_time = 6 + end_index - 3;
      var day =  day_mapping[course.session[k].slice(0, 3)];
      result +=  day + " " + start_time.toString() + ' - ' + end_time.toString();
    }
    return result

  }


  onClickUpdate(sortMethod){
    var rtn = this.slice(sortMethod)
    this.setState({course: rtn[0], courseSlice: rtn[1], pageCount: rtn[2]})
    document.getElementsByClassName("Page1")[0].style.display = "block";
    for(var i = 2; i <= this.state.pageSize; i++)
        document.getElementsByClassName("Page" + i)[0].style.display = "none";
  }

  addCourse(newCourse){
    var selected = this.state.courseSelected
    if(selected.includes(newCourse)){
      selected.splice(selected.indexOf(newCourse), 1);0
    }
    else {
        selected.push(newCourse)
    }
    this.setState({courseSelected: selected})
  }
  slice(sortMethod){
    const courseSize = 10
    const info = ['crse', 'instructor', 'place', 'point', 'session']
    var course = this.props.searchResult
    var courseSlice = []
    var pageCount = 0

    if(sortMethod !== -1){
      course = course.sort(function(x, y){
        return ((x[info[sortMethod]] < y[info[sortMethod]]) ? -1 : ((x[info[sortMethod]] > y[info[sortMethod]]) ? 1 : 0));
      })
    }
    for(var i = 0; i < course.length; i += courseSize){
      courseSlice.push(course.slice(i, i + courseSize))
      pageCount += 1
    }
    return [course, courseSlice, pageCount]
  }

  sendCourse(){
    console.log('Submit Add Course', this.state.courseSelected);
    if(this.state.courseSelected === [])
      return
    for(var i = 0; i < this.state.courseSelected.length; i++){
       this.props.addCourse(this.state.courseSelected[i])
    }
    this.setState({courseSelected: []})
  }

  render(){

    var rtn = this.slice(-1)
    this.state.courseSlice = rtn[1]
    this.state.pageSize = rtn[2]

    return(
      <div>
      <style>{".tag { -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none;}"}</style>
      <style>{".sortTag {width : 10%}"}</style>
      <Table>
        <tr className="tag">
          <td className='sortTag' onClick={() => { this.onClickUpdate(0) }}><Input type='button' value="課程名稱"/></td>
          <td className='sortTag' onClick={() => { this.onClickUpdate(1) }}><Input type='button' value="教師名稱"/></td>
          <td className='sortTag' onClick={() => { this.onClickUpdate(2) }}><Input type='button' value="上課地點"/></td>
          <td className='sortTag' onClick={() => { this.onClickUpdate(3) }}><Input type='button' value="學分數量"/></td>
          <td className='sortTag' onClick={() => { this.onClickUpdate(4) }}><Input type='button' value="修課時間"/></td>
        </tr>
        </Table>
        {
        this.state.courseSlice.map((frag) => {
          return <Table hover
          className={('Page' + (this.state.courseSlice.indexOf(frag) + 1))}
          style={{display: (this.state.courseSlice.indexOf(frag) == 0)? 'block' : 'none'}}
          >
          <tbody>
            {
              frag.map((course) => {
                return <tr className="tag"
                       onClick={() => { this.addCourse(course)}}
                      bgcolor={this.state.courseSelected.includes(course)? "#66FF66" : "#FFFFFF"}
                       >
                      <td className="sortTag">{course.crse.split("/")[0]}</td>
                      <td className="sortTag">{course.instructor.map(place=>{return <div>{place}</div>;})}</td>
                      <td className="sortTag">{course.place.map(place=>{return <div>{place}</div>;})}</td>
                      <td className="sortTag">{course.point}</td>
                      <td className="sortTag">{
                        this.showCourseTime(course).split("\n").map(
                        i => {return <div>{i}</div>;})}
                      </td>
                      </tr>
            })}
            </tbody>
            </Table>
          })}
          <Pagination pageSize={this.state.pageSize}/>
          <Button className='submit' color= 'link' onClick= {() => {this.sendCourse()}}>提交</Button>
      </div>
    );
  }
}



