import React from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, Container,CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import Pagination from './Pagination.js'
import Order from './Order.js'
import Course from './Course.js'
import Todos from './test.js'
import GraduateInfo from './GraduateInfo.js'

export default class Result extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: '1'
    };
  }

  toggle(tab) {

    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  render() {
    return (
      <div >
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.toggle('1'); }}
            >
              搜尋結果
            </NavLink>
          </NavItem>

          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.toggle('2'); }}
            >
              建議課程
            </NavLink>
          </NavItem>

          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '3' })}
              onClick={() => { this.toggle('3'); }}
            >
              選課排序
            </NavLink>
          </NavItem>
        <NavItem>
          <NavLink
            className={classnames({ active: this.state.activeTab === '4' })}
            onClick={() => { this.toggle('4'); }}
          >
            畢業門檻
          </NavLink>
        </NavItem>
      </Nav>

        <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="1">
            <Course searchResult={this.props.searchResult} addCourse={this.props.addCourse}/>
          </TabPane>
          <TabPane tabId="2">
          </TabPane>
          <TabPane tabId="3">
            <Order courseList={this.props.courses} deleteCourse={this.props.deleteCourse} changeCoursePriority={this.props.changeCoursePriority}/>
          </TabPane>
          <TabPane tabId="4">
            <GraduateInfo/>
          </TabPane>


        </TabContent>
      </div>
    );
  }
}



