import React from 'react';
import { Button, ButtonGroup, Table, Form, FormGroup, Input } from 'reactstrap';

export default class Order extends React.Component {
  constructor(props){
    super(props);
    this.handleChange = this.handleChange.bind(this)
    this.state = {courseList: this.props.courseList}
  }

  handleChange(event, course){
    var courseList = this.state.courseList
  //  this.props.changeCoursepriority()
    courseList[courseList.indexOf(course)].priority = event.target.value
    this.setState({courseList: courseList})
  }

  render() {
    const courseMax = 50;
    if(this.state.courseList === []){
      return;
    }
    return (
      <Table>
      {
        this.state.courseList.map((course) => {
          return <tr>
                  <FormGroup>
                    <select value={course.priority} onChange={(e) => this.handleChange(e, course)}>
                    {
                      [...Array(courseMax).keys()].map(i => {
                        return <option value = {i + 1}>{i + 1}</option>
                      })
                    }
                    </select>
                  </FormGroup>
                  <td>{course.crse}</td>
                  <td>{course.instructor}</td>
                  <td>{course.place}</td>
                  <td>{course.point}</td>
                  <td>{course.session}</td>
                </tr>
        })
      }
      </Table>
    );
  }
}



