import React, { Component } from 'react';
import Result from './components/Result'
import './App.css';
/*import  Example  from './components/Example';*/
import  SearchBar from './components/SearchBar';
import Timetable2 from './components/Timetable2';
import Timetable_Wen from './components/Timetable_Wen'
import moment from "moment";
import {Container} from 'reactstrap';

import Modal from 'react-modal';

class App extends Component {

  returnResult(result) {
    console.log('Search Result');
    this.setState({result:result});
    var search_courses = []
    for (var i=0; i<result.data.data.length; i++){
        search_courses.push(result.data.data[i]);
    }
    this.setState({result:search_courses})

    // # day AB 1234 CD 5678 EFGH

    // # 000 00 0000 00 0000 0000
    // course_time = result.data.substring(start, end)
    // console.log('Get Result..\n', result.data);
  }




  constructor() {
    super();

    this.returnResult = this.returnResult.bind(this);
    this.adjustCourses = this.adjustCourses.bind(this);
    this.addCourse     = this.addCourse.bind(this);
    this.deleteCourse  = this.deleteCourse.bind(this);
    this.changeCoursePriority = this.changeCoursePriority.bind(this);

    /* Test */
    var all_courses = []
    var course_test = {'crse': '軟體工程概論', 'instructor':['廖峻鋒'],'_id':'0212' ,'place': ["大仁200102"], 'point': 3, 'session':["0010000000111000000"], 'priority':1 }
    var test = [
      {'crse': '資訊視覺化', 'instructor':['紀明德'],'_id':'5678', 'place': ["資訊140205"], 'point': 3, 'session':  ["0010000000011100000"], 'priority':2 },
      {'crse': '演算法', 'instructor':['沈錳坤'],'_id':'5422', 'place': ["大仁200301"], 'point': 3, 'session':  ["1010001110000000000"], 'priority':3 },
      {'crse': '演算法分析', 'instructor':['沈錳坤'],'_id':'547118', 'place': ["大仁200301"], 'point': 3, 'session':  ["1010000000000001110"], 'priority':5 },
      {'crse': '軟體工程概論', 'instructor':['廖峻鋒'],'_id':'0212' ,'place': ["大仁200102"], 'point': 3, 'session':["0010000000111000000"], 'priority':1 }
    ]

    this.state = {
      courses : all_courses,
      result : test,
      sceduled_courses: [],
    }
  }

  adjustCourses() {
      var courses = this.state.courses;
      courses = courses.sort(function (a, b) {
        return a.priority > b.priority ? 1 : -1;
      });

      var exist_priority = []
      var exist_id = []
      for(var i=0; i<courses.length; i++) {
          if ( exist_priority.includes(courses[i].priority) ) {
              courses[i].priority += 1 ;
          }
          if ( exist_id.includes(courses[i]._id) ) {
            courses.splice(i, 1);
          }
          if (i >= courses[i]) {
              break;
          }
          if (typeof(courses[i]) !== "undefined") {
            exist_priority.push(courses[i].priority);
            exist_id.push(courses[i]._id);
          }

      }


      this.setState({courses:courses});
      this.render();

      let display_courses = this.get_display_courses(courses);
      this.update_timetable_course_cells(display_courses);

  }

  changeCoursePriority(course, new_priority=1) {
      var courses = this.state.courses;
      for(var i=0; i<courses.length; i++) {
          if (course._id === courses[i]._id) {
              courses[i].priority  = new_priority;
          }
          else if (courses[i].priority == new_priority) {
              courses[i].priority += 1;
          }
      }
      this.setState({courses:courses});
      this.adjustCourses();
  }

  addCourse(course) {
    var courses = this.state.courses;

    course['priority'] = 1;
    course['crse'] = course['crse'].split('/')[0]
    for(var i=0; i<courses.length; i++) {
        course['priority'] = Math.max(course['priority'], courses[i].priority+1);
    }
    courses.push(course);
    this.setState({courses:courses});
    this.adjustCourses();
  }

  deleteCourse(course) {
    if (typeof(course) == 'undefined') {
        return
    }
    else if (course.length)
    var courses = this.state.courses;
    for(var i=0; i<courses.length; i++) {
        if(courses[i]._id === course._id) {
            courses.splice(i, 1);
        }
    }
    this.setState({courses:courses});
    this.adjustCourses();
  }

  componentDidMount() {
      Modal.setAppElement(this.el);
  }

  update_timetable_course_cells = (display_courses=[]) => {
    console.log('this.refs.SearchBar', this.refs.SearchBar);

    if( this.refs.SearchBar !== 'undefined' ){
      console.log('this.refs.SearchBar.refs.Timetable', this.refs.SearchBar.refs.Timetable);
      if(this.refs.SearchBar.refs.Timetable !== 'undefined') {
        this.refs.SearchBar.refs.Timetable.update_timetable_course_cells(display_courses);
      }
    }
  }

  get_display_courses = (all_courses=[]) => {
      let display_courses = []
      if ( typeof(this.refs.time_table) !== 'undefined') {
          display_courses = this.refs.time_table.removeConflict(all_courses);
      }
      return display_courses;
  }

  render() {

    let course = {'crse': 'Test', 'instructor':['沈錳坤'],'_id':'5421112', 'place': ["大仁200301"], 'point': 3, 'session':  ["1000001110000000000"], 'priority':3 }
    return (
      <div ref={ref => this.el = ref}>
        <SearchBar ref='SearchBar' returnResult={this.returnResult}   get_display_courses={this.get_display_courses}/>
        <Timetable2 ref='time_table' courses={this.state.courses}  changeCoursePriority={this.changeCoursePriority} />
        <Container>
          <Result searchResult={this.state.result} courses={this.state.sceduled_courses}
          addCourse={this.addCourse} deleteCourse={this.deleteCourse}  changeCoursePriority={this.changeCoursePriority}/>
        </Container>
      </div>
    );
  }
}

export default App;



