import React from 'react';
import TableDragSelect from "react-table-drag-select";
import "react-table-drag-select/style.css";

import {
  Button,
 } from 'reactstrap';


export default class SearchTimetable extends React.Component {

  constructor(props) {
    super();
    this.state = {
        timetable_cells_hasCourse :[
          [false, false, false, false, false, false],
          [false, true,  false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
        ],

        timetable_cells: [
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
          [false, false, false, false, false, false],
        ]
      }

  }

  update_timetable_course_cells = (display_courses=[]) => {

    let timetable_cells = this.state.timetable_cells;
    let timetable_cells_hasCourse = this.state.timetable_cells_hasCourse;

    for(var i=0; i<timetable_cells_hasCourse.length; i++) {
      for(var j=0; j<timetable_cells_hasCourse[0].length; j++){
        timetable_cells_hasCourse[i][j] = false;
      }
    }
    if(display_courses === [])  display_courses = this.props.get_display_courses();
    console.log(display_courses);
    for(var i=0; i<display_courses.length; i++) {
      let time = this.getCourseTime(display_courses[i]);
      for(var d=0; d<time.len; d++) {
        let col = time.day[d];
        let row_start = time.start[d] - 7;
        let row_end = time.end[d] - 7;
        for(var row=row_start; row<row_end; row++) {
            if(row < timetable_cells_hasCourse.length && col < timetable_cells_hasCourse[0].length)
              timetable_cells_hasCourse[row][col] = true;
        }
      }
    }
    this.setState( {timetable_cells_hasCourse : timetable_cells_hasCourse} );

    this.refs.Table.setState(
      {
        selectionStarted: true,
        startRow: 1,
        startColumn: 1,
        endRow: 10,
        endColumn: 10,
      }  );
    this.refs.Table.render();

    this.setState({timetable_cells:timetable_cells});
    this.render();
  }

  getCourseTime(course) {

    var day_mapping = {
      '001':'1',
      '010':'2',
      '011':'3',
      '100':'4',
      '101':'5',
    }

    var start_index = 3;
    var end_index = 0;
    var result = {'day':[], 'start':[],'end':[], 'len':0}
    for(var k=0; k<course.session.length; k++) {
      for(var i=3; i<course.session[k].length; i++) {
        if(course.session[k][i] ===  '1') {
          start_index = i;
          break;
        }
      }

      for(var i=start_index; i<course.session[k].length; i++) {
        end_index = i;
        if(course.session[k][i] === '0') {break;}
      }
      // string.substring(start, end)
      var start_time = 6 + start_index - 3;
      var end_time = 6 + end_index - 3;
      var day =  day_mapping[course.session[k].slice(0, 3)];
      result['day'].push(parseInt(day));
      result['start'].push(start_time);
      result['end'].push(end_time);
      result['len'] += 1
    }
    return result
  }

  render = () => {

      return(
        <div className="SF">
          <TableDragSelect ref='Table' value={this.state.timetable_cells} onChange={this.handleChange}>
            <tr>
              <td disabled />
              <td disabled>一</td>
              <td disabled>二</td>
              <td disabled>三</td>
              <td disabled>四</td>
              <td disabled>五</td>
            </tr>
            <tr>
              <td disabled>08-09</td>
              <td> { this.state.timetable_cells_hasCourse[1][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[1][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[1][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[1][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[1][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>09-10</td>
              <td> { this.state.timetable_cells_hasCourse[2][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[2][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[2][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[2][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[2][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>10-11</td>
              <td> { this.state.timetable_cells_hasCourse[3][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[3][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[3][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[3][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[3][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>11-12</td>
              <td> { this.state.timetable_cells_hasCourse[4][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[4][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[4][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[4][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[4][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>12-13</td>
              <td> { this.state.timetable_cells_hasCourse[5][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[5][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[5][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[5][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[5][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>13-14</td>
              <td> { this.state.timetable_cells_hasCourse[6][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[6][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[6][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[6][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[6][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>14-15</td>
              <td> { this.state.timetable_cells_hasCourse[7][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[7][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[7][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[7][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[7][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>15-16</td>
              <td> { this.state.timetable_cells_hasCourse[8][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[8][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[8][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[8][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[8][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>16-17</td>
              <td> { this.state.timetable_cells_hasCourse[9][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[9][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[9][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[9][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[9][5] ? '課' : null } </td>
            </tr>
            <tr>
              <td disabled>17-18</td>
              <td> { this.state.timetable_cells_hasCourse[10][1] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[10][2] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[10][3] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[10][4] ? '課' : null } </td>
              <td> { this.state.timetable_cells_hasCourse[10][5] ? '課' : null } </td>
            </tr>
            <tr>

            </tr>
          </TableDragSelect>
          <Button size='sm' id="search-timetable-btn"outline color="danger" onClick={this.handleClick}>Reset</Button>
        </div>


      )
  }


  handleChange = timetable_cells => this.setState({ timetable_cells });

  handleClick = () => {
    const timetable_cells = [
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
    ];
    this.setState({ timetable_cells:timetable_cells });
    console.log(this.state)
  };
}

class Mytd extends React.Component {
  render() {
    return (<td> 課 </td>)
  }
}



