import React from 'react';
import { Button, ButtonGroup } from 'reactstrap';

export default class Pagination extends React.Component {
constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
  }
  
  toggle(pageNum){
    for(var i = 1; i <= this.props.pageSize; i++){
      if(i === pageNum){
        console.log(document.getElementsByClassName("Page" + i)[0])
        document.getElementsByClassName("Page" + i)[0].style.display = "block";
      }else{
        document.getElementsByClassName("Page" + i)[0].style.display = "none";
      }
    }
  }

  render() {
    return (
      <ButtonGroup className = "Button">
      {
        [...Array(this.props.pageSize).keys()].map(i => {
            return <Button
                    color = "link"
                    onClick={() => {this.toggle(i + 1)}}
                   >{i + 1}</Button>
          })}
      </ButtonGroup>
    );
  }
}



