import React from 'react';
import Modal from 'react-modal';
import Timetable from 'react-timetable-events';

import Timetable_Wen from './Timetable_Wen'
import classnames from 'classnames';
import moment from "moment";
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, Container,CardTitle, CardText, Row, Col } from 'reactstrap';
import { Table } from 'reactstrap'
import Moment from 'react-moment'

import  _  from 'lodash';
/*
import moment from "moment";
import {Collapse0,Collapse,Input,Button,Row, Col,} from 'reactstrap';
import { slide as Slide } from 'react-burger-menu'
import right_arrow_icon from './right-arrow.svg'
*/

import SlidingPane from 'react-sliding-pane';
import 'react-sliding-pane/dist/react-sliding-pane.css';
import left_arrow_icon from './left-arrow.svg'





export default class Timetable2 extends React.Component {

  handleSlide() {
    this.setState({ slide: !this.state.slide });
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  constructor(props){
    super();

    this.toggle = this.toggle.bind(this);
    this.process_courses = this.process_courses.bind(this);

    var all_courses = props.courses;
    var ok_courses  = this.removeConflict(all_courses);
    var conflict_courses = this.getConflict(all_courses);
    var ok_events  = this.courses_to_events(ok_courses);
    var t = this.getEarliestLatest(ok_events);

    this.state = {
      activeTab: '1',
      isPaneOpen : false,
      earliest: t[0],
      latest: t[1],
      slide : false,
      all_courses: all_courses,
      ok_courses : ok_courses,
      ok_events: ok_events,
      conflict_courses: conflict_courses,
    }

  }

  process_courses(all_courses) {

    var ok_courses  = this.removeConflict(all_courses);
    var conflict_courses = this.getConflict(all_courses);
    var ok_events  = this.courses_to_events(ok_courses);
    var t = this.getEarliestLatest(ok_events);

    this.changepriority = this.changepriority.bind(this);
    this.showConflicts = this.showConflicts.bind(this);
    this.simuTimetable = this.simuTimetable.bind(this);

    this.setState ({
      activeTab: '1',
      isPaneOpen : false,
      earliest: t[0],
      latest: t[1],
      slide : false,
      ok_events: ok_events,
      conflict_courses: conflict_courses,
    })


  }

  isOverlap(course_1, course_2){
    for(var i=0; i<course_1.session.length; i++) {
      for(var j=0; j<course_2.session.length; j++){
        if(course_1.session[i].substring(0,3) === course_2.session[j].substring(0,3)){
          for(var k=3; k<course_1.session[i].length; k++) {
            if(course_1.session[i][k] === '1'  && course_2.session[j][k] === '1') {
              return true;
            }
          }
        }
      }
    }
    return false;
  }

  removeConflict(courses) {

    var ok_courses = _.cloneDeep(courses);
    var ok_courses = ok_courses.sort(function (a, b) {
     return a.priority > b.priority ? 1 : -1;
    });

    for (var i=0; i<ok_courses.length; i++) {
      for(var j=i+1; j<ok_courses.length; j++) {
        if(this.isOverlap(ok_courses[i], ok_courses[j])) {
          ok_courses.splice(j, 1);
          j = j - 1;
        }
      }
    }
    return ok_courses;

  }

  getConflict(courses){

    var conflict_courses = _.cloneDeep(courses);
    var ok_courses = this.removeConflict(courses);

    for(var i=0; i<conflict_courses.length; i++) {
      for(var j=0; j<ok_courses.length; j++) {
        if(conflict_courses[i]._id === ok_courses[j]._id){
          conflict_courses.splice(i, 1);
        }
      }
    }
    return conflict_courses;
  }
  create_event(course, session) {
    var newEvent =
        {
          id: 2,
          name: course.crse,
          type: '',
          location: course.place,
          startTime: '2000-01-01T00:00:00',
          endTime: '2000-01-01T00:00:00',
          priority: course.priority
        }
    var start_index = 3;
    var end_index = 0;

    for(var i=3; i<session.length; i++) {
      if(session[i] ===  '1') {
        start_index = i;
        break;
      }
    }

    for(var i=start_index; i<session.length; i++) {
      end_index = i;
      if(session[i] === '0') {break;}
    }
    // string.substring(start, end)
    var start_time = 6 + start_index - 3;
    var end_time = 6 + end_index - 3;

    var res_start = newEvent['startTime'].split('T');
    var s_time = (start_time < 10) ? ("0" + start_time.toString()) : start_time.toString();
    newEvent['startTime'] = moment(res_start[0] + 'T' + s_time + res_start[1].substring(2));

    var res_end = newEvent['endTime'].split('T');
    var e_time = (end_time < 10) ? ("0" +  end_time.toString()) : end_time.toString();
    newEvent['endTime'] = moment(res_end[0] + 'T' + e_time + res_end[1].substring(2));

    return newEvent;
  }

  courses_to_events(courses){

    var day_mapping = {
      '001':'monday',
      '010':'tuesday',
      '011':'wednesday',
      '100':'thursday',
      '101':'friday',
    }
    var events= {
      monday: [],
      tuesday: [],
      wednesday: [],
      thursday: [],
      friday: []
    };
    for(var i=0; i<courses.length; i++) {
      var days_code = courses[i].session;
      for(var j=0; j<days_code.length; j++) {
        var day = day_mapping[days_code[j].substring(0, 3)];
        events[day].push(this.create_event(courses[i], days_code[j]));
      }
    }
    return events;
  }

  getEarliestLatest(events) {
    var earliest = 24;
    var latest   = 0;
    for (var key in events) {
      if (events.hasOwnProperty(key)) {
          var courses = events[key];
          if(courses.length !== 0 ) {
            for (var i=0; i<courses.length; i++){
              var s_time = courses[i].startTime._i.slice(-8, -6);
              var e_time = courses[i].endTime._i.slice(-8, -6);
              s_time = parseInt(s_time, 10);
              e_time = parseInt(e_time, 10);
              if(s_time < earliest) earliest = s_time;
              if(e_time > latest) latest = e_time;
            }
          }
      }
  }
    return [earliest,latest];
}

  showCourseTime(course) {

    var day_mapping = {
      '001':'週一',
      '010':'週二',
      '011':'週三',
      '100':'週四',
      '101':'週五',
    }

    var start_index = 3;
    var end_index = 0;
    var result = ""
    for(var k=0; k<course.session.length; k++) {
      if(k>=1){
        result += '\n'
      };
      for(var i=3; i<course.session[k].length; i++) {
        if(course.session[k][i] ===  '1') {
          start_index = i;
          break;
        }
      }

      for(var i=start_index; i<course.session[k].length; i++) {
        end_index = i;
        if(course.session[k][i] === '0') {break;}
      }
      // string.substring(start, end)
      var start_time = 6 + start_index - 3;
      var end_time = 6 + end_index - 3;
      var day =  day_mapping[course.session[k].slice(0, 3)];
      result +=  day + " " + start_time.toString() + ' - ' + end_time.toString();
    }
    return result

  }

  changePriorityByCourseName = (course_name) =>{
      let all_course = this.props.courses;
      let course = null;
      for(var i=0; i<all_course.length; i++) {
          if(all_course[i].crse.split('/')[0] === course_name.split('/')[0]) {
              course = all_course[i];
          }
      }
      this.changepriority(course);
      console.log()
  }

  changepriority = (course) =>{
    this.props.changeCoursePriority(course, 1);
    this.process_courses(this.props.courses);
    this.setState({ isPaneOpen: true })
  }

  componentDidMount() {
    Modal.setAppElement(this.el);
  }

  showConflicts = (course) => {
      var oneCourseConf = [];
      this.state.all_courses.map((conflicts) => {
        if(course.crse !== conflicts.crse && this.isOverlap(course,conflicts))
        {
          oneCourseConf.push(conflicts);
        }
      });
      console.log('AAAA', oneCourseConf);
      return oneCourseConf;
      //回傳滑鼠指到的課程的衝堂
    }

    ifYouChange = (course) => {
      //某個衝堂產生的
      var cor = this.showConflicts(course);
      var tmpCourse = _.cloneDeep(this.state.all_courses);
      for(var i=0;i<cor.length;i++)
      {
        for(var j=0;j<tmpCourse.length;j++)
        {
          if(cor[i].crse === tmpCourse[j].crse)
          {
            tmpCourse.splice(j,1);
          }
        }
      }

      tmpCourse = this.removeConflict(tmpCourse);

     for(var x=0;x<tmpCourse.length;x++)
      {
      }
      return tmpCourse;
    }

    simuTimetable = (course) =>
    {
      //比較課表
      var confA = this.showConflicts(course);
      var final = {};
      for(var k=0;k<confA.length;k++)
      {
        var tempCourse = this.ifYouChange(confA[k]);//會產生的課表
        //var before = this.ifYouChange(course);//本來的課表
        var before = _.cloneDeep(this.state.ok_courses); //正常
        for(var s=0;s<tempCourse.length;s++)
        {
        }
        for(var t=0;t<before.length;t++)
        {
        }
        var tempO = {};
        for(var i=0;i<tempCourse.length;i++)
        {
          for(var j=0;j<before.length;j++)
          {
            //  console.log("bef crse",before[j].crse);
            //  console.log("tmp crse",tempCourse[i].crse);
              if(tempCourse[i].crse === before[j].crse)
              {
                tempCourse.splice(i,1);
                before.splice(j,1);
                j = j-1;
                if(tempCourse.length === 0)
                {
                  break;
                }
                if(i > 0)
                {
                  i--;
                }
              //  console.log("leng =>",tempCourse.length);
              }
          }
        }
  //      console.log("confa =>",confA[k].crse);
        tempO["add"] = _.cloneDeep(tempCourse);
        tempO["sub"] = _.cloneDeep(before);
        final[confA[k].crse] = tempO;
      }

      console.log("final change",final);
      return final;
    }




  render() {
    this.state.all_courses = this.props.courses;
    this.state.ok_courses  = this.removeConflict(this.state.all_courses);
    this.state.conflict_courses = this.getConflict(this.state.all_courses);
    this.state.ok_events  = this.courses_to_events(this.state.all_courses);
    var t = this.getEarliestLatest(this.state.ok_events);
    this.state.earliest = t[0];
    this.state.latest = t[1];

        return(
        <div ref={ref => this.el = ref}>
        <div id={'show_timetabel_block'} onClick={() => this.setState({ isPaneOpen: true })} >
          <p className='myp'>顯示</p>
          <img src={left_arrow_icon} id='img' alt="description"/>
          <p className='myp'>課表</p>
        </div>
            <SlidingPane
                className={'some-custom-class'}
                overlayClassName='some-custom-overlay-class'
                isOpen={ this.state.isPaneOpen }
                title='您的課表'
                width='70vw'
                subtitle='Your syllabus.'
                onRequestClose={ () => {
                    // triggered on "<" on left top click or on outside click
                    this.setState({ isPaneOpen: false });
                } }>

              <Nav tabs>
                  <NavItem id="timetable_0">
                    <NavLink
                      className={classnames({ active: this.state.activeTab === '1' })}
                      onClick={() => { this.toggle('1'); }}
                    >
                      未衝堂課表

                    </NavLink>
                  </NavItem>
                  <NavItem id="timetable_1">
                    <NavLink
                      className={classnames({ active: this.state.activeTab === '2' })}
                      onClick={() => { this.toggle('2'); }}
                    >
                      衝堂課列表
                    </NavLink>
                  </NavItem>
                </Nav>
                <TabContent activeTab={this.state.activeTab}>
                  <TabPane tabId="1">
                    <Row>
                      <Col sm="12">

                        {/*    Timetable      */}
                        <div>
                          <Timetable_Wen changePriorityByCourseName={this.changePriorityByCourseName} 
                          simuTimetable={this.simuTimetable} showConflicts={this.showConflicts}
                          display_courses={this.state.ok_courses} earliest={this.state.earliest} latest={this.state.latest}/>
                        </div>

                      </Col>
                    </Row>
                  </TabPane>
                  <TabPane tabId="2">

                    {/*<Row>
                      <Col sm="6">
                        <Card body>
                          <CardTitle>Special Title Treatment</CardTitle>
                          <CardText>With supporting text below as a natural lead-in to additional content.</CardText>
                          <Button>Go somewhere</Button>
                        </Card>
                      </Col>
                      <Col sm="6">
                        <Card body>
                          <CardTitle>Special Title Treatment</CardTitle>
                          <CardText>With supporting text below as a natural lead-in to additional content.</CardText>
                          <Button>Go somewhere</Button>
                        </Card>
                      </Col>
                    </Row>*/}
                    <Table>
                      <thead>
                        <tr>
                            <th>課程名稱</th>
                            <th>教師名稱</th>
                            <th>上課地點</th>
                            <th>課堂時間</th>
                            <th>更換課程</th>
                        </tr>
                      </thead>
                      <tbody>
                      {
                        this.state.conflict_courses.map((course) => {
                            return<tr>
                                <td>{course.crse.split('/')[0]}</td>
                                <td>{course.instructor}</td>
                                <td>{course.place}</td>
                                <td>{
                                  <div>
                                      {this.showCourseTime(course).split("\n").map(i => {
                                          return <div>{i}</div>;
                                      })}
                                  </div>

                                  }</td>
                                <td>
                                <Button id='btn1234' outline color = "black" size = "middle" onClick={() => {this.changepriority(course)}} >更換</Button>
                                </td>
                                {/*<input
                                type="checkbox"
                                className="checkbox"/>*/}
                                </tr>

                              }
                            )
                          }
                        </tbody>
                      </Table>

                  </TabPane>

                </TabContent>




            </SlidingPane>
        </div>
      )
    }
}



