import React from 'react';
import { Card, Button, CardImg, CardTitle, CardText, CardGroup,
 CardSubtitle, CardBody,
 Progress
} from 'reactstrap';



export default class GraduateInfo extends React.Component {
  render() {
    return(

      <div className='GraduateInfo'>
      <CardGroup>
          <Card className="text-center" body outline color="primary">
            <CardBody >
              <CardTitle> <strong> 必修學分 </strong> </CardTitle>

              <div className="text-center">12/50</div>
              <Progress striped value='25'/>

            </CardBody>
          </Card>

          <Card body outline color="primary">
            <CardBody className="text-center">
              <CardTitle> <strong> 選修學分 </strong> </CardTitle>

              <div className="text-center">50/50</div>
              <Progress animated color="success" value='100' />

            </CardBody>
          </Card>

          <Card body outline color="primary">
            <CardBody>
              <CardTitle className="text-center"> <strong> 通識學分 </strong> </CardTitle>

              <div className="text-center">26/28</div>
              <Progress animated value='95'/>

              <CardSubtitle>中文通識</CardSubtitle>
              <CardSubtitle>外文通識</CardSubtitle>
              <CardSubtitle>自然通識</CardSubtitle>
              <CardSubtitle>社會通識</CardSubtitle>
              <CardSubtitle>人文通識</CardSubtitle>
            </CardBody>
          </Card>
        </CardGroup>

        <CardGroup>

        <Card body outline color="primary">
          <CardBody className="text-center">
            <CardTitle> <strong> 體育 </strong> </CardTitle>
            <div className="text-center">1/4(門)</div>
            <Progress value='25'/>
            <CardSubtitle>Card subtitle</CardSubtitle>
          </CardBody>
        </Card>

        <Card body className="text-center" outline color="primary">
          <CardBody>
            <CardTitle> <strong> 服務學習 </strong> </CardTitle>
            <div className="text-center">1/2(門)</div>
            <Progress value='50'/>
            <CardSubtitle>Card subtitle</CardSubtitle>
          </CardBody>
        </Card>

        </CardGroup>
      </div>

    ) }
}



